.event.list {
    a:not(.button) {
        color: var(--text); }
    ul {
        list-style: none;
        padding-left: 0; }
    .heading {
        border-bottom: var(--border-line);
        margin-top: 1em;
        margin-bottom: 1em;
        padding-bottom: .25rem;
        display: flex;
        align-items: baseline;
        @media screen and (min-width: 1023px) {
            margin-bottom: 2em; }
        .headsub {
            text-transform: capitalize;
            font-size: 1.5em; }
        h6 {
            font-weight: 500;
            margin-left: auto; } }
    .row,
    .main,
    .taxo,
    .years,
    .month,
    .lister,
    .yearlist,
    .feature {
        display: grid;
        grid-gap: 1rem;
        @media screen and (min-width: 768px) {
            grid-gap: 2rem; }
        .row {
            grid-template-columns: 3rem auto;
            margin: 1em 0;
            padding: 1em 0; } }
    .item {
        border-bottom: var(--border-line); }
    .box {
        border-right: 1px solid orange;
        width: 3rem;
        height: 3rem;
        .date {
            font-weight: 600;
            font-size: 1.5rem; }
        .month {
            font-size: .9rem;
            margin-top: 0em; } }
    .leads {
        font-size: .9rem; }
    .main {
        grid-template-columns: auto;
        margin-top: 1em;
        .listing {
            display: grid; }
        @media screen and (min-width: 480px) {
            margin-top: 2em;
            .listing {
                grid-template-columns: auto; } }
        @media screen and (min-width: 768px) {
            .listing {
                grid-gap: 0 2em;
                grid-template-columns: auto auto; } }
        @media screen and (min-width: 1023px) {
            margin-top: 3em;
            grid-template-columns: 21% auto 21%;
            .listing {
                grid-template-columns: auto; } } }
    .taxo,
    .years,
    .month, {
        grid-template-columns: auto;
        margin-top: 3em;
        grid-gap: 3rem;
        @media screen and (min-width: 1024px) {
            grid-template-columns: auto 23%; }
        .listevents {
            .row {
                border-bottom: 0;
                margin: 0;
                padding: 1em 0; }
            .box .month {
                margin-top: 0; } } }
    .yearlist {
        grid-gap: 0rem;
        border-bottom: var(--border-line);
        @media screen and (min-width: 768px) {
            padding: 2rem 0;
            grid-template-columns: auto; }
        @media screen and (min-width: 1024px) {
            grid-template-columns: 13rem auto; }
        .evitems {
            display: grid;
            grid-template-columns: auto;
            grid-gap: 1rem;
            @media screen and (min-width: 768px) {
                grid-template-columns: auto auto;
                grid-gap: 2rem; }
            @media screen and (min-width: 1024px) {
                grid-template-columns: auto; } }
        .heading {
            @media screen and (min-width: 1024px) {
                display: block; }
            border-bottom: 0; }
        .item {
            border-bottom: 0; } }
    .monthlist {
        .feature {
            grid-template-columns: auto;
            @media screen and (min-width: 768px) {
                grid-template-columns: auto auto; }
            .thumb,
            .thumb img,
            .slider-con {
                height: 17rem; }
            .item {
                border-bottom: 0; }
            .box {
                border-right: 0px; } }
        .lister {
            grid-template-columns: auto;
            border-top: var(--border-line);
            margin-top: 2rem;
            padding-top: 2rem;
            @media screen and (min-width: 768px) {
                grid-template-columns: 23% auto; } } }
    .termlist {
        .section {
            border-bottom: var(--border-line);
            padding-bottom: 2rem;
            margin-bottom: 3rem; }
        h4 {
            margin-bottom: 1rem; } }
    .listevents {
        margin-bottom: 1.4em;
        .title {
            font-size: 1.05rem; }
        .leads {
            font-size: .85re; } } }
